<template>
  <div
    class="ui-file-upload__wrapper p-card p-p-3 p-mb-3 p-d-flex p-ai-center p-jc-between"
  >
    <ui-progress-spinner v-if="isLoading" />
    <!-- todo: leave this here, move rest to feature components -->
    <p-file-upload
      ref="pFileUpload"
      :mode="'basic'"
      :auto="true"
      :customUpload="true"
      :chooseLabel="label"
      :accept="accept"
      @uploader="upload"
    />
    <ui-button
      v-if="!!downloadUrl"
      class="ui-file-upload__preview-button p-ml-3"
      :label="'Preview'"
      :type="'button'"
      :icon="'pi-eye'"
      @click="togglePreview"
    />
    <ui-overlay-panel ref="overlayPanel">
      <template #content>
        <div class="preview__wrapper">
          <img class="preview__image" :src="downloadUrl" />
        </div>
      </template>
    </ui-overlay-panel>
  </div>
</template>

<style lang="scss" scoped>
.ui-file-upload {
  &__wrapper {
    position: relative;
  }
  &__preview-button {
    width: 50%;
  }
}

.preview {
  &__wrapper {
    width: 20rem;
    height: 15rem;
  }
  &__image {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center center;
  }
}
</style>

<style lang="scss">
.p-fileupload {
  width: 100%;
  &-choose {
    width: 100%;
    .p-button-label {
      line-height: normal;
    }
  }
}
</style>

<script lang="ts">
import { defineComponent, computed, inject } from "vue";
import { useField } from "vee-validate";
import { v4 } from "uuid";
import { firGetDownloadUrl, firUploadFile } from "@/core/firebase/fir.storage";
import { ShowMessageFunc, SHOW_MESSAGE } from "@/models/notification.model";
import { formatBytes } from "@/core/utils/format-bytes";
import { getFirErrorMessage } from "@/core/firebase/fir.core";
import UiProgressSpinner from "./ui-progress-spinner.vue";
import UiOverlayPanel from "./ui-overlay-panel.vue";
import UiButton from "./ui-button.vue";

interface CustomUploaderEvent {
  files: File[];
}
interface PFileUploadInstance {
  clear: () => void;
}

export interface UiFileUploadResult {
  fileRef: string;
  downloadUrl: string;
}

const UiFileUpload = defineComponent({
  components: {
    UiProgressSpinner,
    UiOverlayPanel,
    UiButton
  },
  props: {
    name: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: false,
      default: "Choose"
    },
    refPrefix: {
      type: String,
      required: false,
      default: ""
    },
    accept: {
      type: String,
      required: false
    },
    maxFileSize: {
      type: Number,
      required: false
    }
  },
  setup(props) {
    const { errorMessage, handleChange, meta, resetField, value } = useField(
      props.name
    );
    const showError = computed(() => meta.dirty && !meta.valid);

    const showMessage = inject(SHOW_MESSAGE) as ShowMessageFunc;

    return {
      handleChange,
      errorMessage,
      meta,
      showError,
      resetField,
      showMessage,
      value
    };
  },
  data() {
    return { isLoading: false, downloadUrl: "" };
  },
  methods: {
    clearInner() {
      (this.$refs.pFileUpload as PFileUploadInstance).clear();
    },
    togglePreview(event: Event) {
      (this.$refs.overlayPanel as InstanceType<typeof UiOverlayPanel>).toggle(
        event
      );
    },
    async upload(event: CustomUploaderEvent) {
      const file = event.files[0];
      this.clearInner();

      if (this.accept) {
        const mimeRegex = new RegExp(this.accept.replace("*", "[^\\/,]+"));
        if (!mimeRegex.test(file.type)) {
          this.showMessage({
            type: "failure",
            title: "Invalid file type",
            message: `This control only accepts ${this.accept}`
          });
          return;
        }
      }

      if (this.maxFileSize && file.size > this.maxFileSize) {
        this.showMessage({
          type: "failure",
          title: "Invalid file size",
          message: `Should be less than ${formatBytes(this.maxFileSize)}`
        });
        return;
      }

      const extension = file.name.split(".").pop();
      const fileRef = `${this.refPrefix}${v4()}.${extension}`;
      this.isLoading = true;

      try {
        this.downloadUrl = await firUploadFile(fileRef, file);
        this.isLoading = false;
      } catch (e) {
        this.showMessage({
          type: "failure",
          title: "Cannot upload file",
          message: getFirErrorMessage(e)
        });
        this.isLoading = false;
        return;
      }

      this.showMessage({
        type: "info",
        title: "Image uploaded",
        message: "Check preview to see it"
      });
      this.handleChange(fileRef);
    },
    async forcePreviewCheck() {
      if (!this.value) {
        return;
      }

      try {
        this.downloadUrl = await firGetDownloadUrl(this.value as string);
      } catch (e) {
        this.showMessage({
          type: "failure",
          title: "Cannot get preview",
          message: getFirErrorMessage(e)
        });
      }
    }
  }
});

export default UiFileUpload;
</script>
