<template>
  <div class="ui-dropdown__wrapper">
    <p-dropdown
      :name="name"
      :placeholder="placeholder"
      :options="options"
      :option-label="'label'"
      :option-value="'value'"
      :model-value="inputValue"
      :showClear="true"
      :class="{ 'p-invalid': showError }"
      @update:model-value="innerHandleChange"
      @blur="handleBlur"
    />
    <small class="p-error" v-if="showError">{{ errorMessage }}</small>
  </div>
</template>

<style lang="scss" scoped>
.ui-dropdown {
  &__wrapper {
    position: relative;
    margin-bottom: 1rem;
  }
}
.p-error {
  display: block;
  margin-top: 0.25rem;
  font-size: 0.8rem;
  text-align: right;
}
</style>

<style lang="scss">
.ui-dropdown__wrapper {
  .p-dropdown {
    width: 100%;
  }
}
.p-dropdown {
  &-label {
    line-height: normal;
  }

  &-panel {
    transform: translateX(0.5rem);
  }

  &:not(.p-disabled).p-invalid.p-focus {
    box-shadow: 0 0 0 0.2rem #ffcfcb;
  }
}
</style>

<script lang="ts">
import { useField } from "vee-validate";
import { computed, defineComponent, PropType } from "vue";
import { UiDropdownOptions } from "@/models/dropdown.model";

const UiDropdown = defineComponent({
  props: {
    name: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: false,
      default: ""
    },
    options: {
      type: Array as PropType<UiDropdownOptions<unknown>[]>,
      required: true
    }
  },
  emits: ["change"],
  setup(props, { emit }) {
    const {
      value: inputValue,
      errorMessage,
      handleBlur,
      handleChange,
      meta
    } = useField(props.name);

    const showError = computed(() => meta.dirty && !meta.valid);

    const innerHandleChange = (evt: Event) => {
      handleChange(evt);
      emit("change");
    };

    return {
      innerHandleChange,
      handleBlur,
      errorMessage,
      inputValue,
      meta,
      showError
    };
  }
});

export default UiDropdown;
</script>
