import firebaseApp from "./fir.app";

export const firUploadFile = async (
  fileRef: string,
  file: File
): Promise<string> => {
  const snapshot = await firebaseApp
    .storage()
    .ref(fileRef)
    .put(file);
  return snapshot.ref.getDownloadURL();
};

export const firGetDownloadUrl = async (fileRef: string): Promise<string> =>
  firebaseApp
    .storage()
    .ref(fileRef)
    .getDownloadURL();
