// 1000	kB	kilobyte
// 10002	MB	megabyte
// 10003	GB	gigabyte
// 10004	TB	terabyte
// 10005	PB	petabyte
// 10006	EB	exabyte
// 10007	ZB	zettabyte
// 10008	YB	yottabyte

const prefixes = [
  "",
  "kilo",
  "mega",
  "giga",
  "tera",
  "peta",
  "exa",
  "zetta",
  "yotta"
];

export const formatBytes = (bytes: number): string => {
  if (!bytes) {
    return "0";
  }

  const absBytes = Math.abs(bytes);
  let i = 0;
  while (Math.pow(1000, i) < absBytes) {
    i++;
  }

  const prefix = prefixes[i - 1];
  return `${Number((bytes / Math.pow(1000, i - 1)).toFixed(2))} ${prefix}bytes`;
};
