<template>
  <p-data-table
    :value="value"
    :paginator="true"
    :rows="10"
    :rowsPerPageOptions="[10, 20, 50]"
    :loading="loading"
    removableSort
    v-model:filters="innerFilters"
    filterDisplay="menu"
    stateStorage="session"
    :stateKey="storageKey"
  >
    <template #empty>
      <p class="p-text-center p-mt-4 p-mb-4">
        {{ noDataText }}
      </p>
    </template>
    <template #loading>
      <p class="p-text-center p-mt-4 p-mb-4">
        Loading data, please wait...
      </p>
    </template>
    <p-column
      v-for="column of columns"
      :key="column.field"
      :field="column.field"
      :header="column.header"
      :sortable="column.sortable"
      :dataType="column.dataType"
    >
      <template #body="{data}">
        <slot :name="'body_' + column.field" :fieldValue="data[column.field]">
          {{ data[column.field] }}
        </slot>
      </template>

      <template #filter="{filterModel}" v-if="innerFilters[column.field]">
        <v-switch :value="column.dataType">
          <template #text>
            <input
              class="p-inputtext"
              v-model="filterModel.value"
              :placeholder="column.header"
            />
          </template>
          <template #numeric>
            <input
              type="number"
              class="p-inputtext p-inputnumber-input"
              v-model="filterModel.value"
              :placeholder="column.header"
            />
          </template>
        </v-switch>
      </template>
    </p-column>
  </p-data-table>
</template>

<style lang="scss">
.p-datatable {
  .p-datatable-tbody {
    & > tr {
      & > td {
        vertical-align: middle;
      }
    }
  }
  .p-datatable-loading-overlay {
    display: none;
  }
}
</style>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import { cloneDeep } from "lodash";
import VSwitch from "@/components/directives/v-switch.vue";

export type DataTableColumnDataType = "text" | "numeric" | "date";
export interface DataTableColumn<T> {
  field: keyof T;
  header: string;
  dataType?: DataTableColumnDataType;
  sortable?: boolean;
}

// this should've used `FilterMatchMode` and `FilterOperator`, imported from
// `primevue/api`, but Prime has poorly designed typings
export type DataTableFilterMatchMode =
  | "startsWith"
  | "contains"
  | "notContains"
  | "endsWith"
  | "equals"
  | "notEquals"
  | "in"
  | "lt"
  | "lte"
  | "gt"
  | "gte"
  | "between"
  | "dateIs"
  | "dateIsNot"
  | "dateBefore"
  | "dateAfter";
export type DataTableFilterOperator = "and" | "or";

export interface DataTableFilterConstraint {
  value: unknown;
  matchMode: DataTableFilterMatchMode;
}
export interface DataTableFilter {
  operator: DataTableFilterOperator;
  constraints: DataTableFilterConstraint[];
}

const DataTable = defineComponent({
  components: {
    VSwitch
  },
  props: {
    columns: {
      type: Array as PropType<DataTableColumn<unknown>[]>,
      required: true
    },
    value: {
      type: Array,
      required: true
    },
    filters: {
      type: Object as PropType<Record<string, DataTableFilter>>,
      required: false,
      default: {}
    },
    storageKey: {
      type: String,
      required: true
    },
    loading: {
      type: Boolean,
      required: false,
      default: false
    },
    noDataText: {
      type: String,
      required: false,
      default: "No data found"
    }
  },
  data() {
    const innerFilters = cloneDeep(this.filters);
    return { innerFilters };
  }
});

export default DataTable;
</script>
