
import { defineComponent } from "vue";

interface VSwitchProps {
  value: string | number;
}

// https://github.com/vuejs/vue/issues/8097#issuecomment-486018518
const VSwitch = defineComponent({
  functional: true,
  props: {
    value: { type: [String, Number], required: true }
  },
  render(props: VSwitchProps) {
    const { value } = props;
    const slotFn =
      value in this.$slots ? this.$slots[value] : this.$slots.default;

    return slotFn ? slotFn(this.$attrs) : null;
  }
});

export default VSwitch;
