<template>
  <div class="ui-input-number__wrapper">
    <p-input-number
      :name="name"
      :placeholder="placeholder"
      :class="{ 'p-invalid': showError }"
      :mode="mode"
      :min="min"
      :max="max"
      :prefix="prefix"
      :suffix="suffix"
      :model-value="inputValue"
      @update:model-value="handleChange"
      @blur="handleBlur"
    />
    <small class="p-error" v-if="showError">{{ errorMessage }}</small>
  </div>
</template>

<style lang="scss" scoped>
.ui-input-number {
  &__wrapper {
    margin-bottom: 1rem;
  }
}
.p-error {
  display: block;
  margin-top: 0.25rem;
  font-size: 0.8rem;
  text-align: right;
}
</style>

<style lang="scss">
.p-inputnumber {
  width: 100%;
}
</style>

<script lang="ts">
import { useField } from "vee-validate";
import { defineComponent, PropType, computed } from "vue";

const uiInputNumberModes = ["decimal", "currency"] as const;
const isUiInputNumberMode = (val: unknown) =>
  typeof val === "string" &&
  uiInputNumberModes.includes(val as UiInputNumberMode);

export type UiInputNumberMode = typeof uiInputNumberModes[number];

const UiInputNumber = defineComponent({
  props: {
    name: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: false,
      default: ""
    },
    mode: {
      type: String as PropType<UiInputNumberMode>,
      required: false,
      default: "decimal",
      validator: isUiInputNumberMode
    },
    prefix: {
      type: String,
      required: false
    },
    suffix: {
      type: String,
      required: false
    },
    min: {
      type: Number,
      required: false
    },
    max: {
      type: Number,
      required: false
    }
  },
  setup(props) {
    const {
      value: inputValue,
      errorMessage,
      handleBlur,
      handleChange,
      meta
    } = useField(props.name);

    const showError = computed(() => meta.dirty && !meta.valid);

    return {
      handleChange,
      handleBlur,
      errorMessage,
      inputValue,
      meta,
      showError
    };
  }
});

export default UiInputNumber;
</script>
