<template>
  <base-layout>
    <template #header>
      <base-header />
    </template>
    <template #sidebar>
      <section class="auth-sidebar p-d-flex p-flex-column" v-if="!!currentUser">
        <p class="p-card-title">
          {{ currentUser.displayName }}
        </p>
        <p class="p-card-subtitle">
          {{ currentUser.role }}
        </p>
        <hr class="p-mt-1 p-mb-4" />
        <slot name="sidebar" />
        <ui-button :label="'Log out'" :icon="'pi-sign-out'" @click="logOut" />
      </section>
    </template>
    <template #content>
      <slot name="content" />
    </template>
    <template #footer>
      <base-footer />
    </template>
  </base-layout>
</template>

<style lang="scss" scoped>
.auth-sidebar {
  height: 100%;

  hr {
    width: 100%;
    border: 0;
    box-shadow: #495057 0 0 0px 0.5px;
  }

  button {
    margin-top: auto;
  }
}
</style>

<script lang="ts">
import { computed, defineComponent, inject } from "vue";
import BaseLayout from "./base/base-layout.vue";
import BaseHeader from "./base/base-header.vue";
import BaseFooter from "./base/base-footer.vue";
import UiButton from "@/components/ui/ui-button.vue";
import { useStore } from "vuex";
import { AppStore } from "@/store/interfaces";
import { useRouter } from "vue-router";
import { RootActions } from "@/store/actions";
import { ShowMessageFunc, SHOW_MESSAGE } from "@/models/notification.model";

const AuthorizedLayout = defineComponent({
  components: {
    BaseLayout,
    BaseHeader,
    BaseFooter,
    UiButton
  },
  setup() {
    const { state, dispatch } = useStore() as AppStore;
    const router = useRouter();
    const showMessage = inject(SHOW_MESSAGE) as ShowMessageFunc;

    const currentUser = computed(() => state.currentUser);

    const logOut = async () => {
      try {
        await dispatch(RootActions.SIGN_OUT);
        router.push("/sign-in");
      } catch (error) {
        showMessage({
          type: "failure",
          title: "Cannot log out",
          message: error.message
        });
      }
    };

    return { currentUser, logOut };
  }
});

export default AuthorizedLayout;
</script>
