<template>
  <div class="good-table">
    <ui-progress-spinner v-if="isLoading" />

    <data-table
      :value="goods"
      :columns="columns"
      :filters="filters"
      :storageKey="'vue:seller-good-table'"
      :loading="isLoading"
    >
      <template #body_createdAt="{ fieldValue }">
        {{ timeFormat(fieldValue) }}
      </template>

      <template #body_cost="{ fieldValue }">
        {{ currencyFormat(fieldValue) }}
      </template>

      <template #body_id="{ fieldValue }">
        <div class="p-text-right">
          <ui-button
            :label="''"
            :icon="'pi-pencil'"
            @click="editGood(fieldValue)"
          />
          <ui-button
            :label="''"
            :icon="'pi-trash'"
            dangerous
            class="p-ml-1"
            @click="askDeleteConfirmation(fieldValue)"
          />
        </div>
      </template>
    </data-table>
  </div>
</template>

<style lang="scss" scoped>
.good-table {
  position: relative;
}
</style>

<script lang="ts">
import { computed, defineComponent, inject } from "vue";

import DataTable, {
  DataTableColumn,
  DataTableFilter
} from "@/components/data/data-table.vue";
import { SellerActions } from "@/store/modules/seller/actions";
import { Good } from "@/models/good.model";
import useTimeFormat from "@/composables/useTimeFormat";
import useCurrencyFormat from "@/composables/useCurrencyFormat";
import { useStore } from "vuex";
import { AppStore } from "@/store/interfaces";
import UiButton from "@/components/ui/ui-button.vue";
import UiProgressSpinner from "@/components/ui/ui-progress-spinner.vue";
import {
  AskConfirmationFunc,
  ASK_CONFIRMATION
} from "@/models/confirmation.model";
import { ShowMessageFunc, SHOW_MESSAGE } from "@/models/notification.model";

const GoodTable = defineComponent({
  components: {
    DataTable,
    UiButton,
    UiProgressSpinner
  },
  created() {
    this.$store.dispatch(SellerActions.GET_MY_GOODS);
  },
  setup() {
    const { timeFormat } = useTimeFormat("yyyy LLL dd");
    const { currencyFormat } = useCurrencyFormat();

    const columns: DataTableColumn<Good>[] = [
      {
        field: "title",
        header: "Title",
        dataType: "text",
        sortable: true
      },
      {
        field: "createdAt",
        header: "Created at",
        dataType: "date",
        sortable: true
      },
      {
        field: "cost",
        header: "Cost",
        sortable: true,
        dataType: "numeric"
      },
      {
        field: "id",
        header: ""
      }
    ];
    const filters: Partial<Record<keyof Good, DataTableFilter>> = {
      title: {
        operator: "and",
        constraints: [
          {
            value: null,
            matchMode: "contains"
          }
        ]
      },
      cost: {
        operator: "or",
        constraints: [
          {
            value: null,
            matchMode: "equals"
          }
        ]
      }
    };

    const store = useStore() as AppStore;
    const goods = computed(() => {
      const { goodsList } = store.state.seller;
      return goodsList.all.map(id => goodsList.byId[id]);
    });
    const isLoading = computed(
      () =>
        store.state.seller.isGoodsListLoading ||
        store.state.seller.isDeleteGoodLoading
    );

    const askConfirmation = inject(ASK_CONFIRMATION) as AskConfirmationFunc;
    const showMessage = inject(SHOW_MESSAGE) as ShowMessageFunc;

    return {
      timeFormat,
      currencyFormat,
      columns,
      filters,
      goods,
      isLoading,
      askConfirmation,
      showMessage
    };
  },
  methods: {
    editGood(goodId: string) {
      this.$router.push(`/seller/goods/${goodId}`);
    },
    askDeleteConfirmation(goodId: string) {
      const targetGood = this.$store.state.seller.goodsList.byId[goodId];
      if (!targetGood) {
        return;
      }
      this.askConfirmation({
        header: "Are you sure?",
        message: `You are going to delete "${targetGood.title}"`,
        icon: "pi pi-info-circle",
        rejectLabel: "Cancel",
        acceptLabel: "Delete",
        acceptClass: "p-button-danger",
        accept: () => this.deleteGood(goodId)
      });
    },
    async deleteGood(goodId: string) {
      try {
        await this.$store.dispatch(SellerActions.DELETE_GOOD, goodId);
        this.showMessage({
          type: "success",
          message: "Good was deleted"
        });
      } catch (error) {
        this.showMessage({
          type: "failure",
          title: "Cannot delete good",
          message: error.message
        });
      }
    }
  }
});

export default GoodTable;
</script>
