
import { defineComponent, inject } from "vue";

import UiFieldSet from "@/components/ui/ui-field-set.vue";
import UiProgressSpinner from "@/components/ui/ui-progress-spinner.vue";
import UiForm from "@/components/ui/ui-form.vue";
import UiInputText from "@/components/ui/ui-input-text.vue";
import UiTextarea from "@/components/ui/ui-textarea.vue";
import UiDropdown from "@/components/ui/ui-dropdown.vue";
import UiInputNumber from "@/components/ui/ui-input-number.vue";
import UiFileUpload from "@/components/ui/ui-file-upload.vue";
import UiButton from "@/components/ui/ui-button.vue";

import {
  GoodCreateEditFormValue,
  goodCreateEditValidationSchema
} from "./good-create-edit.model";
import { SellerActions } from "@/store/modules/seller/actions";
import { ShowMessageFunc, SHOW_MESSAGE } from "@/models/notification.model";
import { Good } from "@/models/good.model";
import { mapGetters } from "vuex";
import { RootGetters } from "@/store/getters";

const GoodCreateEdit = defineComponent({
  components: {
    UiFieldSet,
    UiProgressSpinner,
    UiForm,
    UiInputText,
    UiTextarea,
    UiDropdown,
    UiInputNumber,
    UiFileUpload,
    UiButton
  },
  setup() {
    const showMessage = inject(SHOW_MESSAGE) as ShowMessageFunc;
    return { schema: goodCreateEditValidationSchema, showMessage };
  },
  beforeRouteUpdate(to, from, next) {
    this.isEditing = to.params.id !== "create";
    if (this.isEditing) {
      this.goodId = to.params.id as string;
      this.checkGoodForEditing();
    } else {
      (this.$refs.refUiForm as InstanceType<typeof UiForm>).resetForm();
    }
    next();
  },
  data() {
    const goodId = this.$route.params.id as string;
    const isEditing = goodId !== "create";

    return { goodId, isEditing };
  },
  mounted() {
    this.checkGoodForEditing();
  },
  computed: {
    ...mapGetters([RootGetters.CATEGORIES_OPTIONS]),
    isLoading(): boolean {
      const { isGoodCategoriesLoading, seller } = this.$store.state;
      const {
        isCreateGoodLoading,
        isGetGoodForEditLoading,
        isEditGoodLoading
      } = seller;
      return (
        isGoodCategoriesLoading ||
        isCreateGoodLoading ||
        isGetGoodForEditLoading ||
        isEditGoodLoading
      );
    },
    pageTitle(): string {
      return this.isEditing ? "Edit the good" : "Create new good";
    },
    submitButtonLabel(): string {
      return this.isEditing ? "Edit the good" : "Create new good";
    }
  },
  methods: {
    async checkGoodForEditing() {
      if (this.isEditing) {
        try {
          const goodToEdit: Good = await this.$store.dispatch(
            SellerActions.GET_GOOD_TO_EDIT,
            this.goodId
          );
          const { title, description, cost, categoryId, image } = goodToEdit;
          (this.$refs.refUiForm as InstanceType<typeof UiForm>).setValues<
            GoodCreateEditFormValue
          >({
            title,
            description,
            cost,
            categoryId,
            image
          });
          (this.$refs.refFileUpload as InstanceType<
            typeof UiFileUpload
          >).forcePreviewCheck();
        } catch (error) {
          this.showMessage({
            type: "failure",
            title: "Cannot open good",
            message: error.message
          });
          this.$router.push("/seller/goods");
        }
      }
    },
    async handleFormSubmit(formValue: GoodCreateEditFormValue) {
      try {
        const action = this.isEditing
          ? SellerActions.EDIT_GOOD
          : SellerActions.CREATE_GOOD;
        const good: Good = await this.$store.dispatch(action, formValue);
        this.showMessage({
          type: "success",
          title: this.isEditing ? "Good data updated" : "New good created",
          message: good.title
        });
        this.$router.push("/seller/goods");
      } catch (error) {
        this.showMessage({
          type: "failure",
          title: `Cannot ${this.isEditing ? "update" : "create"} good`,
          message: error.message
        });
      }
    }
  }
});

export default GoodCreateEdit;
