
import { useField } from "vee-validate";
import { computed, defineComponent } from "vue";

const UiTextarea = defineComponent({
  props: {
    name: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: false,
      default: ""
    },
    rows: {
      type: Number,
      required: false,
      default: 5
    },
    cols: {
      type: Number,
      required: false,
      default: 30
    },
    autoResize: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  setup(props) {
    const {
      value: inputValue,
      errorMessage,
      handleBlur,
      handleChange,
      meta
    } = useField(props.name);

    const showError = computed(() => meta.dirty && !meta.valid);

    return {
      handleChange,
      handleBlur,
      errorMessage,
      inputValue,
      meta,
      showError
    };
  }
});

export default UiTextarea;
