
import { defineComponent } from "vue";
import AuthorizedLayout from "@/components/layout/authorized-layout.vue";

import { RouterLinkDescriptor } from "@/models/router.model";

const SellerRoot = defineComponent({
  components: {
    AuthorizedLayout
  },
  setup() {
    const links: RouterLinkDescriptor[] = [
      {
        title: "My goods",
        link: "/seller/goods",
        icon: "pi-list"
      },
      {
        title: "Create new good",
        link: "/seller/goods/create",
        icon: "pi-plus-circle"
      },
      {
        title: "My sales",
        link: "/seller/sales",
        icon: "pi-dollar"
      }
    ];

    return { links };
  }
});

export default SellerRoot;
