<template>
  <p-overlay-panel ref="pOverlayPanel" :showCloseIcon="true">
    <slot name="content" />
  </p-overlay-panel>
</template>

<script lang="ts">
import { defineComponent } from "vue";

interface POverlayPanelInstance {
  toggle: (event: Event) => void;
}

const UiOverlayPanel = defineComponent({
  methods: {
    toggle(event: Event) {
      (this.$refs.pOverlayPanel as POverlayPanelInstance).toggle(event);
    }
  }
});

export default UiOverlayPanel;
</script>
