<template>
  <div class="textarea__wrapper">
    <p-textarea
      :placeholder="placeholder"
      :name="name"
      :value="inputValue"
      :autoResize="autoResize"
      :rows="rows"
      :cols="cols"
      :class="{ 'p-invalid': showError }"
      @input="handleChange"
      @blur="handleBlur"
    />
    <small class="p-error" v-if="showError">{{ errorMessage }}</small>
  </div>
</template>

<style lang="scss" scoped>
.textarea {
  &__wrapper {
    margin-bottom: 1rem;
  }
}
.p-error {
  display: block;
  margin-top: 0.25rem;
  font-size: 0.8rem;
  text-align: right;
}
</style>

<script lang="ts">
import { useField } from "vee-validate";
import { computed, defineComponent } from "vue";

const UiTextarea = defineComponent({
  props: {
    name: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: false,
      default: ""
    },
    rows: {
      type: Number,
      required: false,
      default: 5
    },
    cols: {
      type: Number,
      required: false,
      default: 30
    },
    autoResize: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  setup(props) {
    const {
      value: inputValue,
      errorMessage,
      handleBlur,
      handleChange,
      meta
    } = useField(props.name);

    const showError = computed(() => meta.dirty && !meta.valid);

    return {
      handleChange,
      handleBlur,
      errorMessage,
      inputValue,
      meta,
      showError
    };
  }
});

export default UiTextarea;
</script>
