<template>
  <div class="sales">
    <ui-progress-spinner v-if="isLoading" />

    <data-table
      :value="sales"
      :columns="columns"
      :loading="isLoading"
      :storageKey="'vue:seller-sales'"
      :noDataText="'You haven\'t sold any goods yet'"
    >
      <template #body_buyedAt="{ fieldValue }">
        {{ timeFormat(fieldValue) }}
      </template>

      <template #body_cost="{ fieldValue }">
        {{ currencyFormat(fieldValue) }}
      </template>

      <template #body_quantity="{ fieldValue }"> x{{ fieldValue }} </template>

      <template #body_totalCost="{ fieldValue }">
        {{ currencyFormat(fieldValue) }}
      </template>
    </data-table>

    <aside class="p-mt-4 p-text-right">
      <p class="p-mb-2">
        Revenue (this month): {{ currencyFormat(statistics.totalThisMonth) }}
      </p>
      <p>Revenue (all time): {{ currencyFormat(statistics.totalAllTime) }}</p>
    </aside>
  </div>
</template>

<style lang="scss" scoped>
.sales {
  position: relative;
}
</style>

<script lang="ts">
import { computed, defineComponent, onMounted } from "vue";
import DataTable, { DataTableColumn } from "@/components/data/data-table.vue";
import UiProgressSpinner from "@/components/ui/ui-progress-spinner.vue";
import useCurrencyFormat from "@/composables/useCurrencyFormat";
import { AppStore } from "@/store/interfaces";
import { useStore } from "vuex";
import {
  PurchasePresentation,
  PurchaseStatistics
} from "@/models/purchase.model";
import useTimeFormat from "@/composables/useTimeFormat";
import { SellerGetters } from "@/store/modules/seller/getters";
import { SellerActions } from "@/store/modules/seller/actions";

const Sales = defineComponent({
  components: {
    DataTable,
    UiProgressSpinner
  },
  setup() {
    const { currencyFormat } = useCurrencyFormat();
    const { timeFormat } = useTimeFormat("yyyy LLL dd");
    const store = useStore() as AppStore;

    const sales = computed(
      () =>
        store.getters[
          SellerGetters.SALES_PRESENTATION
        ] as PurchasePresentation[]
    );
    const statistics = computed(
      () => store.getters[SellerGetters.SALES_STATISTICS] as PurchaseStatistics
    );

    const isLoading = computed(() => store.state.seller.isSalesListLoading);

    const columns: DataTableColumn<PurchasePresentation>[] = [
      {
        field: "title",
        header: "Title"
      },
      {
        field: "buyedAt",
        header: "Sold at",
        sortable: true,
        dataType: "date"
      },
      {
        field: "cost",
        header: "Cost per item",
        sortable: true,
        dataType: "numeric"
      },
      {
        field: "quantity",
        header: "Quantity",
        sortable: true,
        dataType: "numeric"
      },
      {
        field: "totalCost",
        header: "Total",
        sortable: true,
        dataType: "numeric"
      }
    ];

    onMounted(() => {
      store.dispatch(SellerActions.GET_SALES_LIST);
    });

    return {
      currencyFormat,
      timeFormat,
      sales,
      statistics,
      isLoading,
      columns
    };
  }
});

export default Sales;
</script>
