import { SchemaOf, object, string, number } from "yup";

export interface GoodCreateEditFormValue {
  title: string;
  description: string;
  cost: number;
  image: string;
  categoryId: string;
}

const MIN_TITLE_LENGTH = 3;
const MAX_TITLE_LENGTH = 100;

const MIN_DESC_LENGTH = 100;
const MAX_DESC_LENGTH = 1000;

export const goodCreateEditValidationSchema: SchemaOf<GoodCreateEditFormValue> = object().shape(
  {
    title: string()
      .min(
        MIN_TITLE_LENGTH,
        `Should be at least ${MIN_TITLE_LENGTH} characters`
      )
      .max(
        MAX_TITLE_LENGTH,
        `Shouldn't be more than ${MAX_TITLE_LENGTH} characters`
      )
      .required("Title is required"),
    description: string()
      .min(MIN_DESC_LENGTH, `Should be at least ${MIN_DESC_LENGTH} characters`)
      .max(
        MAX_DESC_LENGTH,
        `Shouldn't be more than ${MAX_DESC_LENGTH} characters`
      )
      .required("Description is required"),
    cost: number()
      .integer("Should be an integer value (in cents)")
      .positive("Should be positive")
      .required("Cost is required"),
    image: string().required("Image is required"),
    categoryId: string()
      .nullable()
      .required("Category is required")
  }
);
